*{
box-sizing: border-box;
}

body,html{
    width:100%;
    height:100%;
    margin:0px;
    font-family: Helvetica;
}


.Blog-image{
    width:100vw;
    margin-top: 1rem;
    height:30vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.Blog-title{
    font-size:20px;
    font-weight: bolder;
    margin: 10px;
}
.Blog-author-name{
    margin-top:1rem;
}
.Blog-paragraph p,h2{
  padding-left:10px;
  padding-right:10px ;
}
p img{
    width: 100vw;
}