
body,html{
  height: 100%;
}

.blogs-list{
    display: flex;
    flex-wrap: wrap;
    padding:1rem;
    justify-content: center;
    min-height: 100%;
  }
  h1{
    margin-left:9rem;
  }
 .blog-card{
    height: 55vh;
    width:20vw;
    margin:1rem;
    position: relative;
    color:white;
  }
  .blogs-list a{
    text-decoration: none;
  }
  
  .blog-img{
    width:20vw;
    height:35vh;
  }
  .blog-title{
      color:black;
      font-weight: bold;
      text-decoration: none;
  }
  
  .blog-button{
    position: absolute;
    bottom:2rem;
  }
  
  .button-wrapper{
    display: flex;
    justify-content: center;
  }
  .page{
    display: flex;
    justify-content: center;
  }
    .line{
    height:1px;
    margin-top:30px;
    margin-left:35px;
    margin-right:35px;
    align-items: center;
    color:white;
    background-color: black;
  }
  .spinner-3:before {
    content: "";
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    margin-top: -30px;
    margin-left: -30px;
    border-radius: 50%;
    border-top: 2px solid coral;
    border-right: 2px solid transparent;
    animation: spinner 0.7s linear infinite;
  }
  
  @keyframes spinner {
    to {
      transform: rotate(360deg);
    }
  }
  @media screen and (min-width:400px) and (max-width:500px) {
  
    .banner-card{
      height: 30vh;
    }
    .title a{
      font-size: 1.5rem;
    }
    .banner-img{
      height: 28vh;
    }
    .banner-title{
      top:10vh;
      left:5vw;
      font-size: 1.3rem;
    }
    .blog-title{
      color:black;
      font-size: 1.3rem;
      font-weight: bold;
      text-decoration: none;
  }
    .banner-text{
      top:15vh;
      left:5vw;
    }
    .blog-card{
      width:90vw;
      height: 40vh;
      margin:0.1rem;
      box-shadow: 0 6px 10px rgba(0, 0, 0, .08), 0 0 6px rgba(0, 0, 0, .05);
      transition: .3s transform cubic-bezier(.155, 1.105, .295, 1.12), .3s box-shadow, .3s -webkit-transform cubic-bezier(.155, 1.105, .295, 1.12);
    }
    .blog-img{
      width:100%;
      height:28vh;
    }
    
    .blog-button{
      width:80%;
    }
    .active{
      margin-top:19%;
      left:0;
      opacity: 1;
